#!/usr/bin/env node
/**
 * /zest:disable command handler
 *
 * Disables remote persistence (stops syncing to Zest)
 */

import { loadSettings, saveSettings } from "../config/settings.js";
import { logger } from "../utils/logger.js";

async function main() {
  try {
    const settings = await loadSettings();

    if (!settings.enableRemotePersistence) {
      console.log("✓ Remote persistence is already disabled");
      return;
    }

    // Disable remote persistence
    settings.enableRemotePersistence = false;
    await saveSettings(settings);

    console.log("✅ Remote persistence disabled");
    console.log("💾 Data will queue locally only (not synced to Zest)");
    console.log("💡 Use /zest:enable to re-enable syncing");
  } catch (error) {
    logger.error("Failed to disable remote persistence", error);
    console.error(
      "❌ Failed to disable: " + (error instanceof Error ? error.message : "Unknown error"),
    );
    process.exit(1);
  }
}

main();
